@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700);
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background: #00ff87; 
}
::-webkit-scrollbar-thumb {
  background: #38003c; 
}
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.Collection {
  text-align: center;
  width: 100%;
}

.Collection ul {
  display: inline;
}

.Collection ul .Dynamic li{
  color: white;
  font-size: 20px;
}

.Collection ul .songs li{
  color: white;
  font-size: 20px;
}

.Collection ul .playlist li{
  color: white;
  font-size: 20px;
}

.Collection ul .albums li{
  color: white;
  font-size: 20px;
}

.Collection ul .language li{
  color: white;
  font-size: 20px;
}

.Collection h3 {
  font-size: 50px;
  display: block;
  text-align: left;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  border-color: aqua;
}

.list {
  display: inline-block;
}
.songs {
    border-radius: 5px;
    background: -webkit-linear-gradient(top left, green, orange);
    background: linear-gradient(to bottom right, green, orange);
    width: 150px;
    height: 100px;
    text-align: left;
    display: inline-flex;
    padding: 10px 5px 5px 10px;
    margin: 10px 10px 10px 10px;
    border: 0px 10px 0px 10px;
}

